<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">
          {{ edit ? "Изменить заметку" : "Добавить заметку" }}
        </h2>
        <div class="modal__block-inputs">
          <label>
            <span>Title</span>
            <input type="text" v-model.trim="title" />
          </label>
          <label>
            <span>Content</span>
            <textarea v-model.trim="text"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btn del" @click="closeModal">
            Отмена
          </button>
          <button class="modal__block-btn edit" @click="addNote" v-if="!edit">
            Добавить
          </button>
          <button class="modal__block-btn edit" @click="editNote" v-else>
            Изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      text: "",
    };
  },

  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.text = this.title = "";
    },
    addNote() {
      if (this.text != "" && this.title != "") {
        const note = {
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", note);
        this.closeModal();
      }
    },
    editNote() {
      if (this.title != "" && this.text != "") {
        const editedsNote = {
          id: this.editedNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNewNote", editedsNote);
      }
      this.closeModal();
    },
  },
  props: {
    edit: Boolean,
    editedNote: Object,
  },
};
</script>

