<template>
  <div class="wrapper">
    <Navbar
    @setSearchValue="search=$event"
    />
    <Notes 
    :notes="filterNotes"
    @id="delNote"
    @delNote="delNote"
    @changeNote="changeNote"
    />
    
    <Model 
    v-show="isModalOpen"
    @closeModal="isModalOpen=false, edit=false" 
    @addNote="addNote"
    :edit="edit"
    :editedNote="editedNote"
    @editedNewNote="editedNewNote"
    />
    
    <button class="add__note" @click="isModalOpen=true" v-show="!isModalOpen">
      <img src="@/assets/images/edit.svg" alt="">
    </button>
  </div>
</template>

<script>

import Navbar from '@/components/Navbar.vue'
import Notes from '@/components/Notes.vue'
import Model from '@/components/Model.vue'


export default {
  components: {
    Navbar,
    Notes,
    Model
  },
  data(){
    return {
      edit:false,
      notes: [],
      isModalOpen:false,
      currentId:1,
      editedNote: null,
      search:''
    }
  },
  mounted(){
    this.getNotes()
    this.getID()
  },
  computed:{
    filterNotes(){
      return this.search ? this.notes.filter(note=>note.title.toLowerCase().includes(this.search.toLowerCase())): this.notes
      
    }
  },
  methods:{
    addNote(note){
      note.id=this.currentId++
      this.notes.push(note)
      this.edit=false
    },
    delNote(id){
      console.log(id);
      let idx=this.notes.findIndex(note => note.id == id)
      this.notes.splice(idx, 1)
    },
    getNotes(){
      const localNotes = localStorage.notes
      if(localNotes){
        this.notes = JSON.parse(localNotes)
      }
    },
    getID(){
      const localId = localStorage.id
      if(localId){
        this.currentId = JSON.parse(localId)
      }},
    changeNote(id){
      this.isModalOpen=this.edit=true
      let currentNote = this.notes.find(note=>note.id==id)
      this.editedNote = currentNote
    },
    editedNewNote(editNote){
      this.notes.forEach(note=>{
        if(note.id==editNote.id){
          note.title=editNote.title
          note.text=editNote.text
          note.date=editNote.date
          
        }
      })
    }
  },
  watch:{
    notes:{
      handler(){
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
    },
      currentId(){
      localStorage.id = JSON.stringify(this.currentId)
    }
  }
}
</script>

